@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
*{box-sizing:border-box;outline:none}html{display:flex;flex-direction:column;min-height:100%}body{display:contents;margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#root{display:contents}
ul{list-style:none;padding:0px}.Header{display:flex;align-items:center;width:100%;height:120px;padding:0 300px}.Header__wrapper{display:flex;align-items:center;justify-content:space-between;width:100%}.Header__menu{display:flex;align-items:flex-start}.Header__logo{display:flex;align-items:flex-end;margin:0 131px 17px 0}.Header__logo-circle{width:20px;height:20px;margin-right:9px;background-color:#9c69e2;border:none;border-radius:20px}.Header__logo-ellipse{width:20px;height:35px;background-color:#f063b8;border:none;border-radius:20px}.Header__links{display:flex}.Header__link{margin-right:47px;text-decoration:none;color:#000}.Header__link:hover{color:#9c69e2}.Header__list-displayOff{display:none}.Header__list-displayOn{display:block}.Header__button{width:180px;height:60px;background:#9c69e2;color:#fff;border-radius:50px;border:none}.Header__button,.Header__link{font-family:Roboto;font-size:18px;cursor:pointer}.Header__burger-button{display:none}@media screen and (max-width: 992px){li{padding:20px 0 20px 32px;border-top:1px solid #9c69e2}.arrow{display:flex;justify-content:space-between;align-items:center}.Header{position:relative;z-index:2;display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;height:130px;padding:0}.Header__wrapper{display:none}.Header__logo{margin:25px 0 0 32px}.Header__burger-wrapper{width:100%;background-color:#fff}.Header__links{flex-direction:column;width:100%}.Header__buttonContainer{display:flex;justify-content:center;height:148px;border-top:1px solid #9c69e2;align-items:center}.Header__link{min-height:75px;font-size:27px;font-weight:500}.Header__burger-button{position:absolute;z-index:2;top:0;right:3%;display:block;width:64px;height:64px;background-color:transparent;cursor:pointer;border:none}.Header__burger-button_closed::after{content:"";position:absolute;top:26px;right:20px;width:24px;height:1.5px;background-color:#000;box-shadow:0 8px 0 0 #000,0 16px 0 0 #000}.Header__burger-button_opened::before,.Header__burger-button_opened::after{content:"";position:absolute;top:30px;right:19px;width:24px;height:2px;background-color:#000}.Header__burger-button_opened::before{transform:rotate(45deg);box-shadow:none}.Header__burger-button_opened::after{transform:rotate(-45deg);box-shadow:none}}
.Footer{display:flex;align-items:center;justify-content:space-between;width:100%;height:197px;padding:0 300px;background-color:#9c69e2}.Footer__links{display:flex;align-items:flex-start}.Footer__logo{display:flex;align-items:flex-end}.Footer__logo-circle{width:20px;height:20px;margin-right:9px;background-color:#fff;border:none;border-radius:20px}.Footer__logo-ellipse{width:20px;height:35px;background-color:#fff;border:none;border-radius:20px}.Footer__links{display:flex;align-items:center}.Footer__link{margin-right:47px;text-decoration:none;color:#000}.Footer__input{width:419px;height:48px;margin-right:33px;border:none;border-radius:4px;padding:10px 15px;font-size:16px}.Footer__button{width:180px;height:60px;background:#eeaa12;border-radius:50px;border:none}.Footer__button,.Footer__link{font-family:Roboto;font-size:16px;color:#fff;cursor:pointer}@media screen and (max-width: 992px){.Footer{padding:0}.Footer__logo{margin:32px}.Footer__links{flex-direction:column;align-items:flex-start}.Footer__link{margin-top:20px}.Footer__input{width:247px}}@media screen and (max-width: 320px){.Footer{flex-wrap:wrap;height:344px}.Footer__input{margin:0 0 22px 0}.Footer__form{display:flex;flex-direction:column;align-items:center;width:100%}}
.CardContent{margin:0;font-family:Roboto;font-size:16px;line-height:160%;color:#4b5d68}
.CardNumber{position:absolute;left:-64px;top:35px;font-family:Roboto;font-weight:bold;font-size:202px;color:#9c69e2}@media screen and (max-width: 320px){.CardNumber{display:none}}
.CardTitle{margin:0;font-family:Roboto;font-weight:400;font-size:25px;color:#212353}
.EventCard{width:390px;height:358px;box-shadow:0px 10px 0px #f5bd3f;border-radius:50px;background-color:#fff3d7}.EventCard__wrapper{position:relative;display:flex;flex-direction:column;justify-content:space-around;padding:41px 61px 41px 113px;width:100%;height:100%}.EventCard__link{text-decoration:none;color:#212353;font-size:16px;font-weight:700}.EventCard__link:hover{color:#9c69e2}@media screen and (max-width: 320px){.EventCard{width:280px}.EventCard__wrapper{padding:41px 30px}}
.CardsContainer{width:1200px;height:585px;padding:97px 120px 130px 150px;background-color:#ffc84d;border-radius:50px}.CardsContainer__cards{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;width:100%}.CardsContainer__buttons{display:flex;justify-content:flex-start;width:100%;margin-top:50px}.CardsContainer__button{width:6px;height:6px;margin-right:15px;padding:0;border-radius:6px;border:none}.CardsContainer__button:hover{width:10px;height:10px;background-color:#f063b8}.CardsContainer__text{font-size:24px}@media screen and (max-width: 992px){.CardsContainer{width:682px}}@media screen and (max-width: 320px){.CardsContainer{width:320px;padding:97px 20px;border-radius:0}}
.MainPage{width:100%;display:flex;flex-direction:column;align-items:center}.MainPage__title{margin:0;margin-bottom:37px;font-size:40px;color:#212353}.MainPage__text{margin:0;margin:0 20px 56px;max-width:649px;font-size:23px;font-weight:400;text-align:center}
.App{flex-grow:1;display:flex;flex-direction:column;width:100%;height:100%}.App__main{position:relative;z-index:1;flex-grow:1;display:flex;flex-direction:column;align-items:center;height:100%;padding:0 300px}@media screen and (max-width: 992px){.App__main{padding:0}}
